.data {
  display: grid;
  justify-items: center;
  align-items: center;
  &__container {
    min-height: 50vh;
    min-width: 70vw;
    border: 1px solid lightgrey;
    padding: 5rem;
    border-radius: 1rem;
    display: grid;
    justify-items: center;
    align-items: start;
    position: relative;

    &--loader {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      display: grid;
      align-items: center;
      justify-items: center;
      background-color: rgba(255, 255, 255, .9);
    }

    &--error {
      color: firebrick;
    }

    &--picker {
      display: grid;
      grid-template-columns: 1fr 2fr;
      grid-column-gap: 1rem;
      align-items: center;

      select {
        padding: 0.5rem 1rem;
        outline: none;
      }

      label {
        color: rgba(0,0,0, .5);
        font-weight: bold;
      }
    }

    table {
      min-width: 40vw;
      th, td {
        padding: 0.5rem 1rem;
        border: 1px solid lightgrey;
      }

      th {
        background-color: rgba(0,0,0,.03);
      }
    }
  }
}
